/* tree rendering based on the briliant solution at http://fractaledmind.com/articles/css-tree/ */

.Tree_tree__1c-DC {
    list-style: none;
    font-size: 30;
}

.Tree_tree__1c-DC,
.Tree_tree__1c-DC * {
    margin: 0;
    padding: 0;
    border-color: #F48C06;
}

.Tree_tree__1c-DC li.Tree_treeElement__2CvD2 {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: var(--tree-spacing-y);
    padding-bottom: var(--tree-spacing-y);
    position: relative;
    padding-left: var(--tree-spacing-x);
}

.Tree_tree__1c-DC li.Tree_treeElement__2CvD2::before {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    border-top: 1px solid;
    border-color: inherit;
    width: var(--tree-spacing-x);
}

.Tree_tree__1c-DC li.Tree_treeElement__2CvD2::after {
    content: '';
    position: absolute;
    left: 0;
    border-left: 1px solid;
    border-color: inherit;
}

.Tree_tree__1c-DC li.Tree_treeElement__2CvD2:last-of-type:not(:only-of-type)::after {
    height: 50%;
    top: 0;
}

.Tree_tree__1c-DC li.Tree_treeElement__2CvD2:first-of-type:not(:only-of-type)::after {
    height: 50%;
    bottom: 0;
}

.Tree_tree__1c-DC li.Tree_treeElement__2CvD2:not(:first-of-type):not(:last-of-type)::after {
    height: 100%;
}

.Tree_tree__1c-DC ul.Tree_treeElement__2CvD2,
.Tree_tree__1c-DC ol.Tree_treeElement__2CvD2 {
    padding-left: var(--tree-spacing-x);
    position: relative;
}

.Tree_tree__1c-DC ul.Tree_treeElement__2CvD2::before,
.Tree_tree__1c-DC ol.Tree_treeElement__2CvD2::before {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    border-top: 1px solid;
    border-color: inherit;
    width: var(--tree-spacing-x);
}

.Tree_tree__1c-DC>li.Tree_treeElement__2CvD2 {
    padding-left: 0;
}

.Tree_tree__1c-DC>li.Tree_treeElement__2CvD2::before,
.Tree_tree__1c-DC>li.Tree_treeElement__2CvD2::after {
    display: none;
}

